import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        service: {
            "customer": 0,  //24h客服
            "customer_phone": 0,//电话客服
            "forget_login_password": 0,//忘记登录密码
            "forget_withdrawal_password": 0,//忘记取款密码
            "account_thaw": 0,//账号解冻
            "withdrawal_not_arrived": 0,//出款未到账
            "update_bank_name": 0,//修改银行卡名称
            "update_bank_number": 0,//修改银行卡卡号
            "deposit_not_arrived": 0,//入款未到账
            "complaints_suggestions": 0,//投诉与建议
            "self_help_offer": 0,//自助优惠申请
            "help_center": 0//常见问题帮助中心
        },
        //问题详情数据
        problems: {},
        errorPage: true,//是否错误页面
        index: 0,//头部下标
    },
    mutations: {
        setData(state, value) {
            state.service = value;
        },
        setProblems(state, value) {
            state.problems = value;
        },
        setError(state, value) {
            state.errorPage = value;
        },
        setIndex(state, value) {
            state.index = value;
        }
    }
})

export default store;