<template>
    <div class="header">
        <div class="headerTop">
            <div class="padding">
                <div class="logo">
                    <img :src="getLogo()" class="logoImg" />
                </div>
                <span class="title">{{ $t("language.title") }}</span>
            </div>
        </div>
        <div class="headerTwo">
            <div class="padding">
                <div class="flexHeader">
                    <div>
                        <div
                            class="btnDiv"
                            :class="[index == 0 ? 'btnActivit' : 'btn1']"
                            @click="goPage('/')"
                        >
                            <!-- 客服自助中心 -->
                            {{ $t("language.service") }}
                        </div>
                        <div
                            class="btnDiv"
                            :class="[index == 1 ? 'btnActivit' : 'btn1']"
                            @click="goPage('/problem')"
                            v-if="sValue.help_center == 1"
                        >
                            {{ $t("language.help") }}
                        </div>
                    </div>
                    <!-- <div class="right">
                        <div
                            class="btnDiv btnR"
                            v-if="sValue.customer == 1"
                            @click="getHelpUrl"
                        >
                            <img
                                src="../assets/image/news.png"
                                class="imgHeader"
                            />
                            <p class="flash">{{ $t("language.servicetow") }}</p>
                        </div>
                        <div
                            class="btnDiv"
                            v-if="sValue.customer_phone == 1"
                            :class="[index == 3 ? 'btnActivit1' : 'btnR']"
                            @click="goPage('/phone')"
                        >
                            <img
                                src="../assets/image/phone.png"
                                class="imgHeader"
                            />
                            <p>{{ $t("language.phone") }}</p>
                        </div>
                    </div> -->
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState({
            sValue: "service",
            index: "index",
        }),
    },
    data() {
        return {};
    },
    methods: {
        goPage(e) {
            if (e == "/") {
                this.$store.commit("setIndex", 0);
            }
            if (e == "/phone") {
                this.$store.commit("setIndex", 3);
            }
            if (e == "/problem") {
                this.$store.commit("setIndex", 1);
            }
            this.$router.push(e);
        },
        //获取24人工客服地址
        getHelpUrl() {
            this.$http.get(this.$api.getHelpUrl).then((result) => {
                console.log(result);
                if (result.code == 0) {
                    if (result.data.domain) {
                        window.open(result.data.domain, "_bank");
                    } else {
                        this.$message.warning("配置出错");
                    }
                }
            });
        },
        getLogo() {
            let lang = this.$config.language;
            let urlTypr = ".png";
            let projectImgUrl =
                this.$config.projectName + "Logo" + urlTypr + "";
            console.log("@/assets/image/lang/" + lang + "/" + projectImgUrl);
            return require("@/assets/image/lang/" + lang + "/" + projectImgUrl);
        },
    },
};
</script>

<style lang="scss" scoped>
.header {
    position: fixed;
    width: 100%;
    top: 0px;
    z-index: 100;
    .headerTop {
        background: #fff;
        height: 60px;
        .title {
            color: var(--themeColor);
            font-size: 24px;
            vertical-align: middle;
        }
        .logo {
            height: 60px;
            display: inline-block;
            vertical-align: middle;
            margin-right: 10px;
            .logoImg {
                height: 60px;
            }
        }
    }
    .headerTwo {
        background: var(--themeColor);
        height: 60px;
        .flexHeader {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #fff;
            height: 60px;
            .btnDiv {
                display: inline-block;
                cursor: pointer;
                height: 60px;
            }
            .btn1:hover {
                background: rgba(255, 255, 255, 0.2);
            }
            .btn1 {
                padding: 0 10px;
                line-height: 60px;
            }
            .btnActivit {
                background: rgba(255, 255, 255, 0.8);
                color: var(--themeColor);
                padding: 0 10px;
                line-height: 60px;
            }
            .btnR {
                padding: 0 20px;
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                p {
                    margin: 0;
                }
            }
            .btnR:hover {
                background: rgba(255, 255, 255, 0.2);
            }
            .btnActivit1 {
                background: rgba(255, 255, 255, 0.8);
                color: var(--themeColor);
                padding: 0 20px;
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                p {
                    margin: 0;
                }
            }
            .flash {
                animation: flashing 2s infinite linear;
            }
            @keyframes flashing {
                0% {
                    color: var(--themeColor);
                    // opacity: 0.5;
                }
                25% {
                    color: #fff;
                }
                50% {
                    color: var(--themeColor);
                }
                75% {
                    color: #fff;
                }
            }
        }
        .right {
            display: flex;
        }

        .imgHeader {
            width: 25px;
            height: 25px;
        }
    }
    .padding {
        margin: 0 18%;
    }
}
</style>