import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routers = [
    {
        path: '/customerService/pc',
        component: () => import('../view/Home.vue'),
    },{
        path: '/',
        component: () => import('../view/Home.vue'),
    }, {
        path: '/help',//问题答案
        component: () => import('../view/ProblemHelper.vue'),
    },
    {
        path: '/forgot/:id',//忘记密码
        component: () => import('../view/ForgotPassword.vue'),
    },
    {
        path: '/problem',//常见问题帮助
        component: () => import('../view/Problem.vue'),
    },
    {
        path: '/phone',//电话客服
        component: () => import('../view/PhoneHele.vue'),
    },
    {
        path: '/setMoney',//存款掉单自助申请
        component: () => import('../view/SetMoney.vue'),
    },
    {
        path: '/payment',//出款未到账
        component: () => import('../view/Payment.vue'),
    },
    {
        path: '/complaint',//投诉与建议
        component: () => import('../view/Complaint.vue'),
    },
    {
        path: '/account',//账号解冻
        component: () => import('../view/Account.vue'),
    },
    {
        path: '/error',//错误页面
        component: () => import('../view/errorPage.vue'),
    },
]
const router = new VueRouter({
    // mode: 'hash',
    // mode: "history",
    routes: routers,
});

router.beforeEach((to, from, next) => {
    document.title = '客服自助中心';
    // if (to.matched.length === 0) { //匹配前往的路由不存在
    //     // from.name ? next({
    //     //     name: from.name
    //     // }) //: next('/error'); //判断此跳转路由的来源路由是否存在，存在的情况跳转到来源路由，否则跳转到404页面
    // } else {
    next(); //如果匹配到正确跳转
    // }
});

export default router;