<template>
    <a-config-provider :locale="locale">
        <div id="app">
            <headerD v-if="error"></headerD>
            <div class="contentPage">
                <router-view></router-view>
            </div>

            <footD v-if="error"></footD>
        </div>
    </a-config-provider>
</template>

<script>
import header from "./components/header";
import foot from "./components/foot";
import zhCn from "ant-design-vue/lib/locale-provider/zh_CN";
import { mapState } from "vuex";

export default {
    name: "App",
    components: {
        headerD: header,
        footD: foot,
    },
    computed: {
        ...mapState({
            error: "errorPage",
        }),
    },
    data() {
        return {
            locale: zhCn,
        };
    },
    created() {
        var str = this.getQuery("s");
        let req;
        if (!str) {
            req = JSON.parse(localStorage.getItem("urlcode"));
            if (!req) {
                this.$store.commit("setError", false);
                this.$router.push("/error");
                return;
            }
        } else {
            req = JSON.parse(window.atob(str));
            localStorage.setItem("urlcode", JSON.stringify(req));
        }
        // console.log(req);
        this.$config.clientCode = req.clientCode;
        this.$config.clientItem = req.clientItem;
        this.$config.username = req.username || "";
        this.$config.language = req.language;
        this.$config.theme = req.theme;
        this.$config.baseURL = req.host;
        this.$config.projectName = req.projectImgUrl;
        this.$config.orgin = req.orgin;
        this.$i18n.locale = this.$config.language || "zh_CN";
        // console.log(this.$config, this.$i18n.locale);
        import(`./assets/css/${req.theme}.css`);
        //  client: "h5"
        // clientCode: "ff10"
        // col: "18"
        // host: "https://gateway.polymerme.com"
        // language: "zh_CN"
    },
    methods: {
        getQuery(name) {
            var r = window.location.search
                .substr(1)
                .match(new RegExp("(^|&)" + name + "=([^&]*)(&|$)"));
            if (r != null) return unescape(r[2]);
            return null;
        },
    },
};
</script>

<style>
body {
    background: #f7f7f7 !important;
    height: 100%;
}
#app {
    background: #f7f7f7;
    height: 100%;
}
.ant-input {
    height: 44px !important;
}
.ant-btn {
    height: 44px !important;
}
.ant-breadcrumb > span:last-child {
    color: var(--themeColor) !important;
}
.ant-input:hover {
    border-color: var(--themeColor) !important;
    border-right-width: 1px !important;
}
.ant-breadcrumb-link {
    cursor: pointer;
}
.contentPage {
    margin-top: 140px;
}
</style>
