import axios from 'axios'
import { message } from 'ant-design-vue'
import config from './config'

const interce = axios.create({

    timeout: 30000,
});

//请求前拦截
interce.interceptors.request.use(configs => {
    // console.log(config);
    configs.headers = {
        'X-Frame-Options': 'SAMEORIGIN'
    };
    configs.headers.contentType = 'application/json';
    configs.headers.clientCode = config.clientCode;
    configs.headers.pc = 1;
    configs.headers.lang = config.language;
    configs.headers.clientItem = config.clientItem;//子平台id字段
    configs.headers.skinCode = config.theme;
    configs.headers.appId = config.theme;
    configs.headers.Authorization = '';// common.getToken();
    return configs
}, (error) => {
    return Promise.reject(error);
})

//响应后拦截
interce.interceptors.response.use((res) => {
    // console.log('请求后', res);
    if (res.status == 200) {
        switch (res.data.code) {
            case 500:
                message.info('请求失败500');
                break;

            default:
                break;
        }
        if (res.data.code != 0) {
            message.info(`${res.data.msg}(${res.data.code})`);
        }
    }
    return res;
}, (error) => {
    message.info('请求失败');
    return Promise.resolve(error.response);
})

export default {
    get(url) {
        return new Promise((resolve, reject) => {
            interce.get(config.baseURL + url).then(res => {
                resolve(res.data || '')
            }).catch(err => {
                reject(err)
            })
        })
    },
    post(url, params) {
        return new Promise((resolve, reject) => {
            let req = {
                'appVer': config.appVer,
                'ver': config.protocolVer,
                'reqId': config.reqId,
                "clientCode": 'ff10',
                'param': params || {}
            };
            interce.post(config.baseURL + url, req).then(res => {
                resolve(res.data || "")
            }).catch(err => {
                reject(err)
            })
        })
    },

}

